@font-face {
  src: url("../../public/fonts/Mont-Bold.otf");
  font-family: MontBold, sans-serif;
}

@font-face {
  src: url("../../public/fonts/Mont-Regular.otf");
  font-family: MontRegular, sans-serif;
}

@font-face {
  src: url("../../public/fonts/Mont-SemiBold.otf");
  font-family: MontSemiBold, sans-serif;
}

@mixin h1-font {
  font-size: 32px;
  line-height: 41px;
  font-family: MontBold, sans-serif;
  letter-spacing: -0.01em;
}

@mixin h2-font {
  font-size: 22px;
  line-height: 31px;
  font-family: MontBold, sans-serif;
  letter-spacing: 0;
}

@mixin h3-font {
  font-size: 20px;
  line-height: 26px;
  font-family: MontSemiBold, sans-serif;
  letter-spacing: 0;
}

@mixin h4-font {
  font-size: 16px;
  line-height: 20px;
  font-family: MontSemiBold, sans-serif;
  letter-spacing: 0;
}
